import React, { Component } from 'react';
import Input from './Input';
import List from './List';


class TodoList extends Component {
    constructor(){
        super();
        this.state = {
            inpValue: '',
            todos:[
                {title:'1',done:false},
                {title:'2',done:true},
                {title:'3',done:true},
                {title:'4',done:false},
            ]
        }
    }
    handleonChange=(e)=>{
        this.setState({inpValue:e.target.value})     
     }
 
     componentDidMount(){
         let todos = localStorage.getItem('todos');
         if(todos){
             this.setState({
                 todos: JSON.parse(todos)
             })
         }
         
     }
    componentDidUpdate(){
       if(this.state.inpValue){
            localStorage.setItem(
                'todos',
                JSON.stringify(this.state.todos)
            )
        }
    }
    addTodo = ()=>{
        if(this.state.inpValue==''){
            return;
        }
        this.setState({
            todos: [
                {
                    title: this.state.inpValue,
                    done: false
                },
                ...this.state.todos
            ]
        })
        this.setState({inpValue:''})

    }
    delTodo = (idx)=>{
        this.setState({
            todos: this.state.todos.filter((item,index)=>index!=idx)
        });
    }
    toggle = (index)=>{
        let newTodos = JSON.parse(JSON.stringify(this.state.todos))
        newTodos[index].done = !newTodos[index].done;
        this.setState({
            todos: newTodos
        })
    }
    render() {
        return (
                <div>
                    <Input  inpValue={this.state.inpValue} 
                        handleChange={this.handleChange}
                        addTodo={this.addTodo}
                    />     
                    <List 
                        todos={this.state.todos}
                        toggle={this.toggle}
                        delTodo={this.delTodo} 
                    />
                </div>    
        );
    }
}

export default TodoList;
